body {
  -webkit-font-smoothing: antialiased;
}

//Typography
h1,
h2,
h3,
h4,
h5,
p,
a,
div,
li {
  font-family: filson-pro, Arial, Helvetica, sans-serif;
}

h2 {
  font-size: 36px;
  font-weight: 900;
  line-height: 40px;
  color: $bountiful-turquoise;
  letter-spacing: 0;
  margin-bottom: 40px;
}

h3 {
  font-size: 33px;
  font-weight: 700;
  color: $bountiful-green;
  letter-spacing: 0;
  line-height: 40px;
  margin-bottom: 25px;
}

.content p {
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 39px;
  margin-bottom: 35px;
  color: #435454;

  @include media-breakpoint-down(md) {
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 25px;
  }
}

hr {
  border: 2px solid rgba(122, 122, 122, 0.1);
  margin: 0 21% 50px 21%;
}

a {
  &:hover {
    text-decoration: none;
  }

  &.btn-secondary {
    color: #fff;
  }

  &.btn-link {
    color: #92bc5c;
  }

  &.light {
    background-color: #e7f0da;
    color: #92bc5c;
    border-color: #e7f0da;
    font-size: 14px !important;
  }
}

.small {
  p {
    font-size: 14px;
    margin-bottom: 10px;
    line-height: 19px;
    padding: 0;
  }
}

.logo {
  width: 160px;
  height: auto;
  margin: 4rem 0 3rem 0;
}

.content {
  section {
    padding: 7rem 0;

    &.beige {
      background: $bountiful-beige;
    }

    div {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  a {
    padding-bottom: 4px;
    border-bottom: solid 2px;

    &:hover {
      text-decoration: none;
    }
  }
}



